#location-info {

  display: flex;
  flex-basis: 2rem;
  justify-content: space-between;
  width: 100%;

}

#main-info {

  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  overflow-y: scroll;
  flex-basis: calc(100% - 2rem);



  .main-column {

    background: hsla(0, 100%, 50%, 40%); //Testing Only. Remove Later.
    flex-basis: 100%;
    height: 100%;
    display: flex;

  }

  #main-col-right {

    column-gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;



    div {

      background: grey; //Testing Only. Remove Later.
      //Developer Note:
      /*

        - Use for setting up grid on "#main-col-right" element
          in viewports with a width >= 768 pixels.

      */
      padding: .2rem;
      border: 1px solid black;
      display: flex;
      flex-direction: column;
      row-gap: .2rem;

      .info-label {

        border-bottom: 1px solid white;

      }

    }

    div:first-of-type {

      flex-basis: 35%;

    }

  }

}



.info-label,
.info-val,
.temp-unit,
#condition-info img[alt] {

  color: white;
  margin: 0;
  display: block;

}

.info-label {

  margin: 0;

}



#city-info {

  flex-basis: 75%;



  #city-val {

    display: flex;
    align-items: center;
    padding-left: .25rem;
    height: 100%;
    font-size: 1rem;
    background: red;

  }

}

#day-info {

  height: 100%;
  width: 25%;



  #day-val {

    width: 100%;
    height: 100%;
    padding: 0 .25rem;
    margin: 0;
    background: green;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;

  }

}



#temp-info,
#condition-info {

  flex-basis: 50%;

}



#temp-info {

  display: flex;
  flex-direction: column;
  align-items: center;
  background: grey;
  justify-content: space-between;
  height: 100%;



  #current-temp-label {

    text-align: center;
    width: 100%;
    padding: .5rem;
    background: grey;

  }



  #temp-unit-labels {

    display: flex;
    width: 100%;

  }

  .temp-unit {

    padding: .25rem;
    flex-basis: 50%;
    background: blue;
    text-align: center;
    border: 1px solid black;
    border-left: none;

  }

  #temp-unit-fahrenheit {

    border-right: none;

  }



  #current-temp-val {

    width: 100%;
    text-align: center;
    font-size: 3rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

  }

}

#condition-info {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;



  #current-condition-label {

    text-align: center;
    background: blue;
    padding: .5rem;
    width: 100%;

  }

  figure {

    margin: 0;
    justify-self: center;
    align-self: center;
    background: radial-gradient(hsl(240, 30%, 65%),
        hsl(240, 30%, 50%));
    display: flex;
    height: 10%;
    width: 100%;
    flex-grow: 1;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;



    .current-condition-image {

      flex-grow: 2;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;



      img {

        align-self: center !important;
        height: 80%;
        display: flex;
        filter: drop-shadow(3px 3px 3px black);

      }

    }



    figcaption {

      text-align: center;
      padding: .25rem 0;
      background: linear-gradient(90deg,
          hsl(280, 30%, 10%),
          hsl(280, 20%, 40%),
          hsl(280, 20%, 40%),
          hsl(280, 30%, 10%));
      width: 100%;

    }

  }

}



@media (min-width: 768px) {

  #city-info {

    flex-basis: 75%;



    #city-val {

      font-size: 2rem;

    }

  }

  #day-info {

    flex-basis: 25%;



    #day-val {

      font-size: 2rem;

    }

  }



  #main-info {

    overflow-x: hidden;
    overflow-y: hidden;
    justify-content: space-between;



    .main-column {

      flex-basis: 50%;

    }

  }

}