#weather-request-form {

  background: hsl(40, 50%, 40%);
  height: 12%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;



  div {

    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;



    input {

      padding: .5rem;
      width: 70%;
      height: 100%;
      font-size: 1.2rem;

    }

    input:focus {

      outline: none;

    }



    button {

      padding: .25rem;
      width: 25%;
      height: 100%;
      font-size: 1.2rem;

    }

  }

}



@media (min-width: 768px) {

  #weather-request-form {

    background: hsl(40, 50%, 40%);



    div {

      transform: scale(1);
      display: flex;
      width: 100%;
      justify-content: center;



      input {

        padding: .25rem;
        width: 70%;
        font-size: 1rem;

      }

      button {

        padding: .25rem;
        width: 20%;
        font-size: 1rem;

      }

    }

  }

}